import Constants from "../constants";
import Dispatcher from "../dispatcher";

export default {
  search(pattern) {
    Dispatcher.dispatch({
      type: Constants.SEARCH,
      payload: {pattern},
    });
  },

  showImageType(imageType) {
    Dispatcher.dispatch({
      type: Constants.SHOW_IMAGE_TYPE,
      payload: {imageType},
    });
  },

  selectImages(images) {
    Dispatcher.dispatch({
      type: Constants.SELECT_IMAGES,
      payload: {images},
    });
  },

  importSelectedImages(dimensions, tags) {
    Dispatcher.dispatch({
      type: Constants.IMPORT_SELECTED_IMAGES,
      payload: {dimensions, tags},
    });
  },

  processSelectedImages() {
    Dispatcher.dispatch({
      type: Constants.PROCESS_SELECTED_IMAGES,
      payload: {},
    });
  },

  cancelProcessSelectedImages() {
    Dispatcher.dispatch({
      type: Constants.CANCEL_PROCESS_SELECTED_IMAGES,
      payload: {},
    });
  },

  applyNonLocalMeans(image) {
    Dispatcher.dispatch({
      type: Constants.APPLY_NON_LOCAL_MEANS,
      payload: {image},
    });
  },

  applySegment(image) {
    Dispatcher.dispatch({
      type: Constants.APPLY_SEGMENT,
      payload: {image},
    });
  },
};



