import React from "react";
import Dialog from "material-ui/lib/dialog";
import {Row, Col} from "../grid/grid";
import TextField from "../text-field/text-field";
import styles from "./dimensions-dialog.css";

export default class DimensionsDialog extends React.Component {
  render() {
    return (
      <Dialog actions={this.props.actions} open={this.props.open} bodyStyle={{background: "#f7f7f7"}} modal>
        <div className={styles.wrapper}>
          <div className={styles.group}>
            <div className={styles.title}>Set dimensions</div>
            <div className={styles.subtitle}>Set dimensions for {this.props.selectedImages.length} {this.props.selectedImages.length === 1 ? "image" : "images"}.</div>
            <Row>
              <Col>
                <TextField label="x" defaultValue="512"/>
              </Col>
              <Col>
                <TextField label="y" defaultValue="512"/>
              </Col>
              <Col>
                <TextField label="z" defaultValue="1"/>
              </Col>
            </Row>
          </div>
          <div className={styles.group}>
            <div className={styles.title}>Tags</div>
            <div className={styles.subtitle}>Add tags for these images.</div>
            <Row>
              <Col>
                <TextField defaultValue="depth=12 modality=sem"/>
              </Col>
            </Row>
          </div>
        </div>
      </Dialog>
    );
  }
}

DimensionsDialog.propTypes = {
  actions: React.PropTypes.array,
  selectedImages: React.PropTypes.array,
  open: React.PropTypes.bool,
};



