import React from "react";
import Table from "material-ui/lib/table/table";
import TableBody from "material-ui/lib/table/table-body";
import TableHeader from "material-ui/lib/table/table-header";
import TableHeaderColumn from "material-ui/lib/table/table-header-column";
import TableRow from "material-ui/lib/table/table-row";
import TableRowColumn from "material-ui/lib/table/table-row-column";
import ImageActions from "../actions/image-actions";

export default class UnprocessedImagesTable extends React.Component {
  constructor() {
    super();
    this.handleRowSelection = this.handleRowSelection.bind(this);
  }
  selectedImages(selectedRows) {
    switch (selectedRows) {
      case "all":
        return this.props.images;
      case "none":
        return [];
      default:
        return selectedRows.map(index => this.props.images[index]);
    }
  }

  // TODO: The selectAll functionality is a bit fucked
  handleRowSelection(selectedRows) {
    ImageActions.selectImages(this.selectedImages(selectedRows));
  }

  render() {
    const rows = this.props.images.map((image) =>
      <TableRow key={image.id} selected={image.selected}>
        <TableRowColumn>{image.name}</TableRowColumn>
        <TableRowColumn />
        <TableRowColumn>{image.tags.join(" ")}</TableRowColumn>
        <TableRowColumn>{image.createdAt.toDateString()}</TableRowColumn>
      </TableRow>
    );

    return (
      <Table fixedHeader selectable multiSelectable onRowSelection={this.handleRowSelection} >
        <TableHeader enableSelectAll>
          <TableRow>
            <TableHeaderColumn>Name</TableHeaderColumn>
            <TableHeaderColumn>Dimensions</TableHeaderColumn>
            <TableHeaderColumn>Tags</TableHeaderColumn>
            <TableHeaderColumn>Date</TableHeaderColumn>
          </TableRow>
        </TableHeader>
        <TableBody deselectOnClickaway={false}>
          {rows}
        </TableBody>
      </Table>
    );
  }
}

UnprocessedImagesTable.propTypes = {
  images: React.PropTypes.array.isRequired,
};



