import React from "react";
import SelectField from "material-ui/lib/select-field";
import MenuItem from "material-ui/lib/menus/menu-item";
import NavBar from "../nav-bar/nav-bar";
import {Row, Col} from "../grid/grid.es6.jsx";
import ImageActions from "../../actions/image-actions";
import styles from "./process-images.css";

export default class processSelectedImages extends React.Component {
  constructor(props) {
    super(props);

    this.state = {previewSrc: props.selectedImage.src};

    // Bindings
    this.applyNonLocalMeans = this.applyNonLocalMeans.bind(this);
    this.applySegment = this.applySegment.bind(this);
  }

  applyNonLocalMeans() {
    ImageActions.applyNonLocalMeans(this.props.selectedImage);
    // HACK!!!
    this.selectVariant("images/non-local-means-512x512.tif");
  }

  applySegment() {
    ImageActions.applySegment(this.props.selectedImage);
    // HACK!!!
    this.selectVariant("images/segmented-512x512.tif");
  }

  selectVariant(src) {
    this.setState({previewSrc: src});
  }

  render() {
    const leftAction = <a onTouchTap={ImageActions.cancelProcessSelectedImages}>&larr;</a>;

    return (
      <div>
        <NavBar title="Process image" left={leftAction} />

        <div className={styles.imageSelectorToolbar}>
          <Row vAlign="center" hAlign="center" width="60%" gapless>
            <Col width="10%" className={styles.previous}>
              <a>&lsaquo;</a>
            </Col>
            <Col className={styles.dropdown}>
              <SelectField value={this.props.selectedImage.id} underlineStyle={{borderColor: "#d3d3d3"}} iconStyle={{fill: "#555"}}>
                {this.props.images.map(image => <MenuItem key={image.id} value={image.id} primaryText={image.name} />)}
              </SelectField>
            </Col>
            <Col width="10%" className={styles.next}>
              <a>&rsaquo;</a>
            </Col>
          </Row>
        </div>

        <div className={styles.variants}>
          <ul>
            <li><img src={this.props.selectedImage.src} height="50" width="50" onClick={this.selectVariant.bind(this, this.props.selectedImage.src)}/></li>
            {this.props.selectedImage.variants.map(variant =>
              <li key={variant.src}><img src={variant.src} height="50" width="50" onClick={this.selectVariant.bind(this, variant.src)} /></li>
            )}
          </ul>
        </div>

        <div className={styles.content}>
          <Row>
            <Col>
              <div className={styles.functionHeader}>Function</div>
              <div className={styles.functions}>
                <div className={styles.function} onClick={this.applyNonLocalMeans}>Non-local Means</div>
                <div className={styles.function} onClick={this.applySegment}>Segment</div>
              </div>
            </Col>
            <Col>
              <div className={styles.preview}>
                <img src={this.state.previewSrc} />
              </div>
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}

processSelectedImages.propTypes = {
  images: React.PropTypes.array.isRequired,
  selectedImage: React.PropTypes.object.isRequired,
};



