import Constants from "../constants";
import Dispatcher from "../dispatcher";
import {Store} from "flux/utils";

class ImageStore extends Store {
  constructor(dispatcher) {
    super(dispatcher);
    this.newImages = [
      {id: 4, name: "Well 4", dimensions: null, tags: [], variants: [], modifiedAt: new Date(), createdAt: new Date(), selected: false, src: "images/original-512x512.tif"},
      {id: 5, name: "Well 5", dimensions: null, tags: [], variants: [], modifiedAt: new Date(), createdAt: new Date(), selected: false, src: "images/original-512x512.tif"},
    ];
    this.importedImages = [
      {id: 1, name: "Well 1", dimensions: {x: 512, y: 512, z: 1}, tags: ["depth=1", "modality=ct"], variants: [], modifiedAt: new Date(), createdAt: new Date(), selected: false, src: "images/original-512x512.tif"},
      {id: 2, name: "Well 2", dimensions: {x: 512, y: 512, z: 1}, tags: ["depth=6", "modality=sem"], variants: [], modifiedAt: new Date(), createdAt: new Date(), selected: false, src: "images/original-512x512.tif"},
      {id: 3, name: "Well 3", dimensions: {x: 512, y: 512, z: 1}, tags: ["depth=2", "modality=ct"], variants: [], modifiedAt: new Date(), createdAt: new Date(), selected: false, src: "images/original-512x512.tif"},
    ];

    this.currentView = Constants.IMAGE_LIBRARY_VIEW;
    this.imageType = Constants.UNPROCESSED_IMAGE_TYPE;
    this.searchPattern = "";
    this.matchesSearchPattern = this.matchesSearchPattern.bind(this);
  }

  getNewImages() {
    return this.newImages.filter(this.matchesSearchPattern);
  }

  getImportedImages() {
    return this.importedImages.filter(this.matchesSearchPattern);
  }

  getImageType() {
    return this.imageType;
  }

  getSelectedImages() {
    const images = this.imageType === Constants.UNPROCESSED_IMAGE_TYPE ? this.newImages : this.importedImages;
    return images.filter(image => image.selected);
  }

  getCurrentView() {
    return this.currentView;
  }

  canImportSelectedImages() {
    return this.getSelectedImages().every(image => image.dimensions !== null);
  }

  matchesSearchPattern(image) {
    if (this.searchPattern.length === 0) {
      return true;
    }

    return image.name.toLowerCase().includes(this.searchPattern) ||
      image.tags.some(t => t.toLowerCase().includes(this.searchPattern)) ||
      image.variants.some(v => v.name.toLowerCase().includes(this.searchPattern));
  }

  __onDispatch(action) {
    switch (action.type) {
      case Constants.SEARCH:
        this.searchPattern = action.payload.pattern.trim().toLowerCase();
        break;

      case Constants.SHOW_IMAGE_TYPE: {
        const imageType = action.payload.imageType;
        if (this.imageType === imageType) {
          return;
        }

        this.imageType = imageType;
        break;
      }

      case Constants.SELECT_IMAGES:
        this.newImages.forEach(image => image.selected = false);
        this.importedImages.forEach(image => image.selected = false);
        action.payload.images.forEach(image => image.selected = true);
        break;

      case Constants.IMPORT_SELECTED_IMAGES: {
        const dimensions = action.payload.dimensions;
        const tags = action.payload.tags;
        const selectedImages = this.getSelectedImages();

        selectedImages.forEach(image => {
          if (dimensions) {
            image.dimensions = dimensions;
          }
          if (tags) {
            image.tags = tags;
          }
        });

        if (this.canImportSelectedImages()) {
          selectedImages.forEach(image => image.selected = false);
          this.newImages = this.newImages.filter(image => !selectedImages.includes(image));
          this.importedImages = this.importedImages.concat(selectedImages);
          if (this.newImages.length === 0) {
            this.imageType = Constants.PROCESSED_IMAGE_TYPE;
          }
          this.currentView = Constants.IMAGE_LIBRARY_VIEW;
        } else {
          this.currentView = Constants.IMPORT_VIEW;
        }
        break;
      }

      case Constants.PROCESS_SELECTED_IMAGES:
        if (this.getSelectedImages().length === 0) {
          return;
        }

        this.currentView = Constants.PROCESS_IMAGES_VIEW;
        break;

      case Constants.CANCEL_PROCESS_SELECTED_IMAGES:
        this.currentView = Constants.IMAGE_LIBRARY_VIEW;
        break;

      case Constants.APPLY_NON_LOCAL_MEANS:
        if (!this.applyFunction(action.payload.image, "Non-local means", "images/non-local-means-512x512.tif")) {
          return;
        }
        break;

      case Constants.APPLY_SEGMENT:
        if (!this.applyFunction(action.payload.image, "Segment", "images/segmented-512x512.tif")) {
          return;
        }
        break;

      default:
        return;
    }

    this.__emitChange();
  }

  applyFunction(image, name, src) {
    if (image.variants.findIndex(variant => variant.name === name) !== -1) {
      return false;
    }

    image.variants.push({name, src});
    return true;
  }
}

const instance = new ImageStore(Dispatcher);
export default instance;



