import React from "react";
import NavBar from "./nav-bar/nav-bar";
import Tabs from "material-ui/lib/tabs/tabs";
import Tab from "material-ui/lib/tabs/tab";
import RaisedButton from "material-ui/lib/raised-button";
import Constants from "../constants";
import UnprocessedImagesTable from "./unprocessed-images-table";
import ProcessedImages from "./processed-images/processed-images";
import ImageActions from "../actions/image-actions";
import Paper from "material-ui/lib/paper";
import Toolbar from "material-ui/lib/toolbar/toolbar";
import ToolbarGroup from "material-ui/lib/toolbar/toolbar-group";
import TextField from "./text-field/text-field";
import {Col} from "./grid/grid";
import styles from "./styles/layout.css";

export default class ImageLibrary extends React.Component {
  constructor() {
    super();

    // Bindings
    this.showUnprocessedImages = this.showUnprocessedImages.bind(this);
    this.showProcessedImages = this.showProcessedImages.bind(this);
    this.importSelectedImages = this.importSelectedImages.bind(this);
    this.processSelectedImages = this.processSelectedImages.bind(this);
    this.search = this.search.bind(this);
  }

  importSelectedImages() {
    ImageActions.importSelectedImages(null, null);
  }

  processSelectedImages() {
    ImageActions.processSelectedImages();
  }

  showUnprocessedImages() {
    ImageActions.showImageType(Constants.UNPROCESSED_IMAGE_TYPE);
  }

  showProcessedImages() {
    ImageActions.showImageType(Constants.PROCESSED_IMAGE_TYPE);
  }

  search(pattern) {
    ImageActions.search(pattern);
  }

  render() {
    // TODO: set default tab to match current store
    const highlightColour = "#58c0a0";

    const verb = this.props.selectedTab === Constants.UNPROCESSED_IMAGE_TYPE ? "Import" : "Process";
    const action = this.props.selectedTab === Constants.UNPROCESSED_IMAGE_TYPE ? this.importSelectedImages : this.processSelectedImages;

    return (
      <div>
        <NavBar title="INSIGHT" />
        <Tabs value={this.props.selectedTab}>
          <Tab
            label={`New [${this.props.unprocessedImages.length}]`}
            onActive={this.showUnprocessedImages}
            style={{background: "#f9f9f9", color: (this.props.selectedTab === Constants.UNPROCESSED_IMAGE_TYPE ? highlightColour : "#1e1e1e")}}
            value={Constants.UNPROCESSED_IMAGE_TYPE}
          >
            <Paper className={styles.content} zDepth={1}>
              <UnprocessedImagesTable images={this.props.unprocessedImages} />
            </Paper>
          </Tab>
          <Tab
            label={`Library [${this.props.processedImages.length}]`}
            onActive={this.showProcessedImages}
            style={{background: "#f9f9f9", color: (this.props.selectedTab === Constants.UNPROCESSED_IMAGE_TYPE ? highlightColour : "#1e1e1e")}}
            value={Constants.PROCESSED_IMAGE_TYPE}
          >
            <Paper className={styles.content} zDepth={1}>
              <ProcessedImages images={this.props.processedImages} />
            </Paper>
          </Tab>
        </Tabs>

        <div className={styles.fixedFooter}>
          <Toolbar style={{backgroundColor: "#fff"}}>
            <ToolbarGroup float="left">
              <Col>
                <TextField label="Search" onChange={this.search}/>
              </Col>
            </ToolbarGroup>

            <ToolbarGroup float="right">
              <RaisedButton label={`${verb} selected`} onTouchTap={action} primary />
            </ToolbarGroup>
          </Toolbar>
        </div>

      </div>
    );
  }
}

ImageLibrary.propTypes = {
  unprocessedImages: React.PropTypes.array.isRequired,
  processedImages: React.PropTypes.array.isRequired,
  selectedTab: React.PropTypes.string.isRequired,
};



