import React from "react";
import ImageLibrary from "./image-library";
import ProcessImages from "./process-images/process-images";
import FlatButton from "material-ui/lib/flat-button";
import RaisedButton from "material-ui/lib/raised-button";
import Constants from "../constants";
import ImageActions from "../actions/image-actions";
import ImageStore from "../stores/image-store";
import DimensionsDialog from "./dimensions-dialog/dimensions-dialog";

import ThemeManager from "material-ui/lib/styles/theme-manager";
import InsightTheme from "../insight-material-ui-theme";
import themeDecorator from "material-ui/lib/styles/theme-decorator";

function getStateFromStores() {
  return {
    newImages: ImageStore.getNewImages(),
    importedImages: ImageStore.getImportedImages(),
    selectedImages: ImageStore.getSelectedImages(),
    currentView: ImageStore.getCurrentView(),
    imageType: ImageStore.getImageType(),
  };
}

@themeDecorator(ThemeManager.getMuiTheme(InsightTheme))
export default class Main extends React.Component {
  constructor() {
    super();

    this.state = getStateFromStores();

    // Bindings
    this.importSelectedImages = this.importSelectedImages.bind(this);
  }

  componentDidMount() {
    this.storeToken = ImageStore.addListener(this.storeDidChange.bind(this));
  }

  componentWillUnmount() {
    this.storeToken.remove();
  }

  storeDidChange() {
    this.setState(getStateFromStores());
  }

  importSelectedImages() {
    ImageActions.importSelectedImages({x: 512, y: 512, z: 1}, ["depth=12", "modality=sem"]);
  }

  render() {
    switch (this.state.currentView) {
      case Constants.IMAGE_LIBRARY_VIEW:
      case Constants.IMPORT_VIEW: {
        const actions = [
          <FlatButton label="Cancel" onTouchTap={ImageActions.cancelProcessSelectedImages} secondary/>,
          <RaisedButton label="Import" onTouchTap={this.importSelectedImages} primary />,
        ];
        return (
          <div>
            <ImageLibrary
              unprocessedImages={this.state.newImages}
              processedImages={this.state.importedImages}
              selectedTab={this.state.imageType}
            />
            <DimensionsDialog
              actions={actions}
              selectedImages={this.state.selectedImages}
              open={this.state.currentView === Constants.IMPORT_VIEW}
              modal
            />
          </div>
        );
      }
      case Constants.PROCESS_IMAGES_VIEW: {
        return (
          <ProcessImages
            images={this.state.selectedImages}
            selectedImage={this.state.selectedImages[0]}
          />
        );
      }
      default: {
        return null;
      }
    }
  }
}



