import React from "react";
import {Row, Col} from "../grid/grid";
import {Media, Figure, Body, Title} from "../media/media";
import styles from "./processed-images.css";
import ImageActions from "../../actions/image-actions";

export default class ProcessedImages extends React.Component {
  constructor(props) {
    super(props);
    this.state = {selectedImages: this.getSelectedImages()};
  }

  getSelectedImages() {
    return this.props.images.filter(image => image.selected);
  }

  toggleSelection(image) {
    let selectedImages = this.state.selectedImages.slice();
    if (selectedImages.indexOf(image) >= 0) {
      selectedImages = selectedImages.filter(i => i !== image);
    } else {
      selectedImages.push(image);
    }

    this.setState({selectedImages});
    ImageActions.selectImages(selectedImages);
  }


  isSelected(image) {
    return this.state.selectedImages.indexOf(image) >= 0;
  }

  render() {
    const images = this.props.images.map((image) =>
      <Col width="50%" key={image.id}>
        <Media className={this.isSelected(image) ? styles.selectedImage : styles.image} onClick={this.toggleSelection.bind(this, image)}>
          <Figure className={styles.thumbnail}>
            <img src={image.src} width="60" height="60" />
          </Figure>
          <Body className={styles.detail}>
            <Title className={styles.title}>{image.name}</Title>
            <div>Tags: {image.tags.join(" ")}</div>
            <div>Functions: {image.variants.map(v => v.name).join(", ")}</div>
            <div>Last modified: {image.modifiedAt.toDateString()}</div>
          </Body>
        </Media>
      </Col>
    );

    return (
      <div className={styles.collection}>
        <Row outerGutter>
          {images}
        </Row>
      </div>
    );
  }
}

ProcessedImages.propTypes = {
  images: React.PropTypes.array.isRequired,
};



