import React from "react";
import classNames from "classnames/bind";
import styles from "./text-field.css";

export default class TextField extends React.Component {
  constructor() {
    super();

    this.onChange = this.onChange.bind(this);
  }

  onChange(event) {
    if (this.props.onChange) {
      this.props.onChange(event.target.value);
    }
  }

  render() {
    const cx = classNames.bind(styles);
    const classes = cx("wrapper", {stacked: this.props.stacked, noLabel: !this.props.label});

    return (
      <div className={classes}>
        <label className={styles.label}>{this.props.label}</label>
        <input className={styles.input} type="text" defaultValue={this.props.defaultValue} onChange={this.onChange}/>
      </div>
    );
  }
}

TextField.propTypes = {
  label: React.PropTypes.string,
  stacked: React.PropTypes.bool,
  defaultValue: React.PropTypes.string,
  onChange: React.PropTypes.func,
};

TextField.defaultProps = {
  stacked: false,
};



