import React from "react";
import {Row, Col} from "../grid/grid.es6.jsx";
import styles from "./nav-bar.css";

export default class NavBar extends React.Component {
  render() {
    return (
      <Row className={styles.navBar} vAlign="center" gapless>
        <Col className={styles.left} width="10%">
          {this.props.left}
        </Col>
        <Col>
          <h1 className={styles.title}>{this.props.title}</h1>
        </Col>
        <Col className={styles.right} width="10%">
          {this.props.right}
        </Col>
      </Row>
    );
  }
}

NavBar.propTypes = {
  title: React.PropTypes.string,
  left: React.PropTypes.node,
  right: React.PropTypes.node,
};



