import React from "react";
import classNames from "classnames/bind";
import styles from "./grid.css";

class Row extends React.Component {
  render() {
    const cx = classNames.bind(styles);
    const inlineStyles = this.props.width ? {flexBasis: this.props.width, width: this.props.width, minWidth: this.props.width} : {};
    const classes = cx(
      "row",
      this.props.className,
      `row--vAlign-${this.props.vAlign}`,
      `row--hAlign-${this.props.hAlign}`,
      {
        outerGutter: this.props.outerGutter,
        gapless: this.props.gapless,
        equalHeights: this.props.equalHeights,
        debug: this.props.debug,
      }
    );

    return (
      <div className={classes} style={inlineStyles}>
        {this.props.children}
      </div>
    );
  }
}

Row.propTypes = {
  className: React.PropTypes.string,
  children: React.PropTypes.node,
  gapless: React.PropTypes.bool,
  outerGutter: React.PropTypes.bool,
  equalHeights: React.PropTypes.bool,
  hAlign: React.PropTypes.oneOf(["left", "center", "right"]),
  vAlign: React.PropTypes.oneOf(["top", "center", "bottom"]),
  debug: React.PropTypes.bool,
  width: React.PropTypes.string,
};

Row.defaultProps = {
  gapless: false,
  outerGutter: false,
  hAlign: "left",
  vAlign: "top",
  equalHeights: false,
  width: "100%",
};

class Col extends React.Component {
  render() {
    const cx = classNames.bind(styles);
    const inlineStyles = this.props.width ? {flexBasis: this.props.width, maxWidth: this.props.width} : {};
    const classes = cx(
      "col",
      this.props.className,
      (this.props.vAlign ? `col--vAlign-${this.props.vAlign}` : false),
      {
        debug: this.props.debug,
      }
    );

    return (
      <div className={classes} style={inlineStyles}>
        {this.props.children}
      </div>
    );
  }
}

Col.propTypes = {
  className: React.PropTypes.string,
  children: React.PropTypes.node,
  vAlign: React.PropTypes.oneOf(["top", "center", "bottom"]),
  width: React.PropTypes.string,
  debug: React.PropTypes.bool,
};

module.exports = {Row, Col};



